import React from 'react';
import Map from './map';
import Myorders from './myorders';

class App1 extends React.Component {
  constructor(props)
  {
    super(props);
    this.state={
      isCourier:false,
      isParcel:false,
      isRental:false,
      isOrder:false,
      isBack:false
    }
  }
  clickCourier=()=>{
    this.setState({ isCourier: true });
  }
  clickParcel=()=>{
    this.setState({ isParcel: true });
  }
  clickRental=()=>{
    this.setState({ isRental: true });
  }
  clickType=()=>{
    this.setState({isOrder:true});
  }
  render()
  {
    let res;
    let isCourier1=this.state.isCourier;
    let isParcel1=this.state.isParcel;

    res = <div className="wrapper">
    <div className="">
    <div className="row">
    <div className="col-md-6 col-xs-12 text-center">

    <div className="info-box bg-danger">
    <span className="info-box-icon" onClick={()=>{window.location.replace('/Booking/myorders')}}><i className="fa fa-arrow-left fa-md"></i></span>

    <div className="info-box-content">
    <span className="info-box-text" style={{fontSize:'26px'}}>Services
    
    </span>
    <div className="progress">
    <div className="progress-bar" style={{width: '100%'}}></div>
    </div>
    <span className="progress-description">
    Choose your type of Delivery 
    </span>
    </div>
    </div>

    <section name="Courier">
    <button class="buttonborder draw" onClick={this.clickCourier}>
    <div class="row">
    <div class="col-md-3 col-xs-12">
    <img src="./dist/img/courier.png" style={{
      height: '90px',
      width: '100px',
      marginTop:'10%'
    }} alt="" />

    </div>
    <div class="col-md-9 col-xs-12">
    <h1>Courier</h1>
    <p>Intracity door to door 2-wheeler parcel service and Intercity courier service.</p>
    </div>
    </div>
    </button>
    </section>


    <section name="Parcel">
    <button class="buttonborder draw notification" onClick={this.clickParcel} disabled>
    <span class="badge">coming soon!</span>
    <div class="row">
    <div class="col-md-3 col-xs-12">
    <img src="./dist/img/parcel.png" style={{
      height: '90px',
      width: '100px',
      marginTop:'10%'
    }} alt="" />

    </div>
    <div class="col-md-9 col-xs-12">
    <h1>Parcel</h1>
    <p>We deliver your valuable parcels with maximum safety. You can send your parcel with <strong>sharing</strong> mode.</p>
    </div>
    </div>
    </button>
    </section>


    <section name="Rental">
    <button class="buttonborder draw notification" onClick={this.clickRental}  disabled>
    <span class="badge">coming soon!</span>    
    <div class="row">
    <div class="col-md-3 col-xs-12">
    <img src="./dist/img/rental.png" style={{
      height: '90px',
      width: '100px',
      marginTop:'10%'
    }} alt="" />

    </div>
    <div class="col-md-9 col-xs-12">
    <h1>Rental</h1>
    <p>We will arrange your parcel rental where you want to drop.</p>
    </div>
    </div>
    </button>
    </section>

    </div>

    <div className="col-md-6 col-xs-12 hidemobile" style={{ height: '100vh', width: '100%' }}>
    <img src="./dist/img/user slide screen - 4.jpg" width="100%" height="100%" alt="" />
    </div>
    <aside className="control-sidebar control-sidebar-dark">
    </aside>
    </div>
    </div>
    </div>;

    if(isCourier1)
    {
      return <Map
      google={this.props.google}
      center={{lat: 9.919136, lng: 78.118868}}
      height='600px'
      zoom={15}
      isType='courier'
      mapHeader='Courier'
      />
    }
    else if(isParcel1)
    {
      return <Map
      google={this.props.google}
      center={{lat: 9.919136, lng: 78.118868}}
      height='600px'
      zoom={15}
      isType='parcel'
      mapHeader='Parcel'
      {...this.props}
      />
    }
    else if(this.state.isBack || this.state.isOrder)
    {
      return <Myorders />
    }    
    else
      return (res);
  }
}

export default App1;
 // background: 'linear-gradient(to right, #30CFD0 0%, #330867 100%)',
 //    WebkitBackgroundClip: 'text',
 //    WebkitTextFillColor: 'transparent' 