import React from 'react';
import Category from './category';
import Header from '../useradmin/header';
import Nav from '../useradmin/nav';
import { withRouter } from 'react-router-dom'
import { Route, Switch, Redirect } from 'react-router-dom';
import { withGoogleMap, GoogleMap, withScriptjs, Marker,Autocom } from "react-google-maps";
import {isMobile} from 'react-device-detect';
import Geocode from "react-geocode";
import Map from "./drivermap";
import BModal from "./modal";
import {MemoizedOrder} from "./orderlist";

const axios = require('axios');

class App1 extends React.Component {
  constructor(props)
  {
    super(props);
    this.isPay=false
    this.clickType = this.clickType.bind(this);
    this.mapRefresh = this.mapRefresh.bind(this);
    this.orderlist = this.orderlist.bind(this);
    this.state={
      nList:0,
      data:[],
      cdata:[],
      rdata:[],
      amount:0,
      isRating: false,
      isCOP: false,
      isCOD: false,
      isOnline: false,
      tempAmt:0,
      tempId:'',tempRefno:'',
      isRefresh:false,isNew:false,tempOTP:'',
      tempDrivername:'',tempMobile:'',tempVehicle:'',tempPickup:0,
      tempLat:0,tempLng:0
    }
    const timer=setInterval(this.orderlist,10000);
  }

  ratingList(){    
    axios.get(global.serverURL + "/api/courier/checkRating", {
      params: {
        "usermobile": global.mobno===''? "9095204586":global.mobno
      }
    })
    .then((response) => {
      return response.data.data;
    })
    .then(data => {  
      if(data!==undefined) 
        this.setState({rdata:data,isRating:true})
    }).catch(error => {
      this.setState({isRating:false}) 
      console.log(error);
    });

  }

  orderlist(cond)
  {
    axios.get(global.serverURL + "/api/courier/myorders", {
      params: {
        "mobno": global.mobno===''? "9095204586":global.mobno
      }
    })
    .then((response) => {
      //console.log(response)
      return response.data.data;
    })
    .then(data => {
      if(cond!==undefined)
        this.setState({cdata:data});
      this.setState({ nList: data.length, data:data, isRefresh:!this.state.isRefresh });
      this.ratingList();
    }).catch(error => {
      this.setState({ nList: 0,data:[] });      
    });
  }

  onlinePay=(data)=>
  {
    this.setState({tempPickup:data.driver1.isPickup,tempAmt:data.driver1.NetTotal,tempId:data.driver1.BookingId,tempOTP:(data.driver1.isPickup==0?data.driver1.otp:data.driver1.recotp)});
  }

  driverinfo=(data)=>
  {
    this.setState({tempDrivername:data.driver1.driver_name,tempMobile:data.driver1.Drivermobile,tempVehicle:data.driver1.vehicle_number,tempLat:data.driver1.dlatitude,tempLng:data.driver1.dlongitude});
  }
  componentDidMount()
  {
    this.orderlist('1');
  }
  mapRefresh()
  {
    this.orderlist('1');
  }

  shouldComponentUpdate( nextProps, nextState ) {
    return true
  }

  clickType=(type,res,mobno)=>{
    if(type==='cancelbooking')
    {
      let conf=window.confirm('Press "OK" to cancel this booking!\nIf you have done online payment, amount will be refunded within 5-7 business days.');
      if(conf)
      {
        axios.post(global.serverURL + "/api/courier/cancelBooking",  {
          "bid": res.id,"mobno":mobno.usermobno,usertype:'user'
        }
        )
        .then(response => {
          if(response.status==="success")
          {
            this.orderlist('1');
          }
          return response.data;
        }).catch(error => {
          console.log(error);
        });
      }
//else alert('false');
}
else if(type==='online')
{
  this.openCheckout();
}
else if(type==='new')
{
  this.setState({isNew:true});
}
}

openCheckout=()=> {
  let id1=this.state.tempId;
  let options = {
    "key": "rzp_live_A1Emq5hXcFECUk",
"amount": this.state.tempAmt*100, // 2000 paise = INR 20, amount in paisa
"name": "Donkeycargo Pvt Ltd.,",
"description": "Online Shipping System",
"image": "./dist/img/DonkeyUser.png",
"handler": (response)=> {
  alert("Transaction completed successfully!");

  axios.post(global.serverURL + "/api/courier/updatePaymode",  {
    "id": id1,"paymode":"online","refno":response.razorpay_payment_id
  }
  )
  .then((response) => {
    this.setState({isRefresh:true});
    this.orderlist('1');
    return response.data.data;
  }).catch(error => {
    console.log(error);
  });

},
"prefill": {
  "name": "Enter name",
  "email": "sample@gmail.com"
},
"notes": {
  "address": "Enter address"
},
"theme": {
  "color": "#F37254"
}
};

let rzp = new window.Razorpay(options);
rzp.open();
}

getsidebar(e){
  console.log(e)
}

render()
{
  let res;
  let res1;
  const places1 = [{
    "id": 1,            
    "name": "Maduraii",
    "latitude": this.state.tempLng,
    "longitude": this.state.tempLat
  }];
  const myList=this.state.data
  const OrderList=()=><MemoizedOrder data={myList} clickType={this.clickType} onlinePay={this.onlinePay} driverinfo={this.driverinfo} />

  let lat1=parseFloat(this.state.tempLat,10)
  let lng1=parseFloat(this.state.tempLng,10)
  const buttonstylefocus={
    cursor:'pointer',
    textAlign: 'center',
    display: 'inline-table',
    padding: '13px 20px',
    borderRadius: '10px',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    fontWeight: '700',
    border: '1px solid #f4511e',
    color: '#fff',
    background: '#f4511e',
    boxShadow: '0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 20px 29px 0 rgba(0, 0, 0, 0.19)',
    wordWrap: 'break-word'
  };
  const buttonstyle={
    cursor:'pointer',
    textAlign: 'center',
    display: 'inline-table',
    padding: '13px 20px',
    borderRadius: '10px',
    color: '#f4511e',
    fontSize: '16px',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    border: '1px solid #f4511e',
    fontWeight: '700',
    textDecoration:'none',
    background: 'transparent',
    wordWrap: 'break-word'
  };


  res1 = <div style={{paddingBottom:'100px'}}>  
  <div className="row">      
  <div className="col-12">
  <OrderList />

  </div>

  </div>
  </div>
  ;

  let cls='is-active'
  let body1='You have '+this.state.nList+' New Order(s)'
  const disp=<span>Pickup OTP</span>
  const disp1=<span>Drop OTP</span>

  res = <div className="wrapper">      
  <Header render={res1} panelheader='My Orders' panelbody={body1} />
  {this.state.isRating?<BModal open={true} data={this.state.rdata}/>:''}


  <div class="modal fade" id="driverModal" tabindex="1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div className="modal-dialog  modal-dialog-centered">

  <div className="modal-content">
  <div className="modal-body" style={{padding:'0.5rem'}}>
  <div className="row">
  <div className="col-12 text-center mb-2">

  <div class="card card-primary card-outline">
  <div class="card-body box-profile">
  <div class="">
  <img class="profile-user-img img-fluid img-circle"
  src="dist/img/avatar5.png"
  alt="User profile picture" />
  </div>

  <h3 class="profile-username text-center">Mr. {this.state.tempDrivername}</h3>

  <p class="text-muted text-center">Madmoney Driver</p>

  <ul class="list-group list-group-unbordered mb-3 text-left">
  <li class="list-group-item">
  <b>Mobile No.</b> <a class="float-right">{this.state.tempMobile}</a>
  </li>
  <li class="list-group-item">
  <b>Vehicle No.</b> <a class="float-right">{this.state.tempVehicle}</a>
  </li>
  <li class="list-group-item">
  <b>Rating</b> 
  <a class="float-right">
  <div>
  <i class="fa fa-star"></i>
  <i class="fa fa-star"></i>
  <i class="fa fa-star"></i>
  <i class="fa fa-star-half-o"></i>
  <i class="fa fa-star-o"></i>
  </div>  
  </a>
  </li>
  </ul>

  </div>
  </div>

  </div>
  </div>
  <div className="modal-footer" style={{padding:'0.5rem'}}>
  <button type="button" className="btn btn-primary" data-toggle="modal" data-target="#mapModal">Where's Driver?</button>
  <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
  </div>
  </div>

  </div>

  </div>
  </div>

  <div class="modal fade" id="mapModal" tabindex="1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div className="modal-dialog modal-lg modal-dialog-centered">

  <div className="modal-content">
  <div className="modal-body" style={{padding:'0.5rem'}}>
  <div className="row">
  <div className="col-12 text-center mb-2">

  <div class="">
  <div class="">

  <Map
  center={{lat: lng1, lng: lat1}}
  zoom={11}
  places={places1}
  height='600px'
  />

  </div>
  </div>

  </div>
  </div>
  <div className="modal-footer" style={{padding:'0.5rem'}}>
  <button type="button" className="btn btn-primary" onClick={this.mapRefresh}>Refresh</button>
  <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
  </div>
  </div>

  </div>

  </div>
  </div>


  <div id="myModal2" tabindex="-1" className="modal fade" role="dialog">
  <div className="modal-dialog  modal-dialog-centered">

  <div className="modal-content">
  <div className="modal-header">
  <h4 className="modal-title">Setup Payment</h4>
  <button type="button" className="close" data-dismiss="modal">&times;</button>
  </div>
  <div className="modal-body">
  <div className="row">
  <div className="col-12">
  <a href="#" style={this.state.isCOD ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => this.clickType("cod")} data-dismiss="modal">
  <span style={{ display: 'grid' }}><i className="fa fa-truck fa-flip-horizontal fa-3x"></i>
  Cash on Delivery</span></a>
  </div>

  <div className="col-12">
  <a href="#" style={this.state.isOnline ? buttonstylefocus : buttonstyle} className="orange-back-link col-12 mt-2 form-control" onClick={() => this.clickType("online")} data-dismiss="modal">
  <span style={{ display: 'grid' }} data-dismiss="modal"><i className="fa fa-credit-card fa-3x"></i>
  Online Payment</span></a>
  </div>

  </div>
  <div className="modal-footer">
  <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
  </div>
  </div>

  </div>
  </div>
  </div>

  <div id="myModal3" tabindex="-1" className="modal fade" role="dialog">
  <div className="modal-dialog  modal-dialog-centered">
  <div className="modal-content">
  <div className="modal-header">
  <h4 className="modal-title">{this.state.tempPickup==0?disp:disp1}</h4>
  <button type="button" className="close" data-dismiss="modal">&times;</button>
  </div>
  <div className="modal-body">
  <div className="row">
  <div className="col-12 text-center mb-2">
  {this.state.tempPickup==0?disp:disp1} is <span class="bg-danger">{this.state.tempOTP}</span>
  </div>
  </div>
  <div className="modal-footer">
  <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
  </div>
  </div>

  </div>
  </div>
  </div>

  </div>

  ;


  if(this.state.isNew)
  {
    return <Category />
  }
  else    return (res);
}
}

export default withRouter(App1);