import React from 'react';
import Category from './category';
import $ from 'jquery';
import Map from "./multimarker100";
import Amountcalc from "./amountcalc";
import Map1 from "./map";

import { TextField,TextareaAutosize  } from '@material-ui/core';
import {validateEmail,clear,validateFields,customValidation} from '../common.js'
const axios = require('axios');

class App1 extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
      product:'Select Product',recname:'',recadd1:'',recadd2:'',recmobno:'',noi:'',remarks:'',isError:false,products:'',isBack:false,isSubmit:false,
      courier:'',nDocs:0,nBox:0,DL1:'',DL2:'',DB1:'',DB2:'',BL1:'',BB1:'',BH1:'',BL2:'',BB2:'',BH2:'',
      error:{product:'',recname:'',recadd1:'',recadd2:'',recmobno:'',noi:'',remarks:''}
    }      
  }
  
  onChange = ( event ) => {
    this.setState({ [event.target.name]: event.target.value });
};

onValidation=(cb)=>{
    const emptyFields=[
    {key:'recname',errVal:'Enter receiver name'},
    {key:'recadd1',errVal:'Enter Flat No./ House No. Street name'},
    {key:'recadd2',errVal:'Enter Area name/ Landmark'},
    {key:'recmobno',errVal:'Enter receiver mobile number'},
    {key:'noi',errVal:'Select No. of Items'},
    {key:'remarks',errVal:'Enter Item description and consignment value'}
    ]
    const data=validateFields(this.state,emptyFields)
    if(data.product==='Select Product')
      customValidation(data,'product','Please select product')
      if(data.recmobno!=='' && data.recmobno.length!==10)
      customValidation(data,'recmobno','Invalid mobile number')
    if(data.noi==='Select No. of Items')
      customValidation(data,'noi','Select No. of items')

    this.setState({data})
    return data.isError
  }

renderRedirect = (type) => {
  let data=this.state;
  if(type==='submit') 
  {
    if(!this.onValidation())
    {
      this.setState({isSubmit:true})
    }
  }
  else
  {
    if(type==='back') 
      this.setState({ isBack: true,isNext:false });
    else if(type==='next') 
      this.setState({ isBack: false,isNext:true });
  }
}

componentDidMount() {
  axios.get(global.serverURL+"/common/ProductType/Courier")
  .then((response) => {
    return response.data;
  })
  .then(data => {
    let teamsFromApi = data.map(team => { return {value: team.ProductType, display: team.ProductType} })
    this.setState({products:teamsFromApi})
  }).catch(error => {
    console.log(error);
  });
};

static defaultProps = {
  address: 'Madurai East, Kamarajapuram Rd, Kamrajapuram, Tamil Nadu 625009, India',
  address1: 'Madurai East, Kamarajapuram Rd, Kamrajapuram, Tamil Nadu 625009, India',
  fromlat: '9.925523',
  fromlng: '78.140323',
  tolat: '9.935103',
  tolng: '78.103698',
  isLocal: true,
  isDomestic:false
};

render()
{
  let res;
  let res1;

  const loadProduct=this.state.products&&this.state.products.map((team) => {
    return(<option key={team.value} value={team.value}>{team.display}</option>)
  })

  const places1 = [{
    "id": 1,            
    "name": this.props.address,
    "latitude": this.props.fromlat,
    "longitude": this.props.fromlng
  },{
    "id": 2,
    "name": this.props.address1,
    "latitude": this.props.tolat,
    "longitude": this.props.tolng
  }];

  res = <div className="wrapper">
  <div className="">
  <div className="row">
  <div className="col-md-6 col-xs-12 text-center">

  <div className="card card-success card-outline">
  <div className="card-header">

  <h3 className="card-title text-center">
  <div className="row">
  <div className="col-3 text-left">
  <div onClick={()=>this.renderRedirect('back')}><i className="fa fa-arrow-left"></i></div>
  </div>
  <div className="col-6">
  <span style={{marginTop:'10px',fontFamily:'fantacy',fontWeight:'500',fontSize:'28px',
  background: 'linear-gradient(to right, darkviolet 0%, #f4511e 100%)',
  WebkitBackgroundClip: 'text',
  WebkitTextFillColor: 'transparent',marginLeft:'-10px' }}> Local </span>
  </div>
  <div className="col-3">
  
  </div>
  </div>
  </h3>
  </div>

  <div className="card-body">
  <div className="input-group mb-2">
  <div className="input-group-prepend">
  <button type="button" className="btn btn-success" style={{width: "70px"}}>From</button>
  </div>
  <input type="text" className="form-control" onChange={ this.onChange } disabled value={ this.props.address }/>
  </div>

  <div className="input-group mb-2">
  <div className="input-group-prepend">
  <button type="button" className="btn btn-danger" style={{width: "70px"}}>To</button>
  </div>
  <input type="text" className="form-control" onChange={ this.onChange } disabled value={ this.props.address1 }/>
  </div> 


  <div className="form-group">
  <label className="select2text">Product</label>
  <select className="form-control" style={{width: '100%'}} placeholder="Product" name="product" onChange={this.onChange}>
  <option key="select" value="Select Product">Select Product</option>
  {loadProduct}  
  </select>
  {this.state.error.product}
  </div>

  <TextField color="green" className="col-12 mb-2" type="text" name="recname" onChange={this.onChange} id="standard-basic" label="Receiver Name" />
  {this.state.error.recname}
  
  <TextField className="col-12 mb-2" type="text" name="recadd1" onChange={this.onChange} onChange={this.onChange} id="standard-basic" label="Flat No./ House No. Street name" />
  {this.state.error.recadd1}

  <TextField className="col-12 mb-2" type="text" name="recadd2" onChange={this.onChange} onChange={this.onChange} id="standard-basic" label="Area name/ Landmark" />
  {this.state.error.recadd2}

  <TextField className="col-12 mb-2" type="text" name="recmobno" onChange={this.onChange} onChange={this.onChange} id="standard-basic" label="Receiver mobile number" inputProps={{maxLength: 10}} />
  {this.state.error.recmobno}

  <div className="form-group">
  <label className="select2text mt-2">Number of Items</label>
  <select className="form-control" style={{width: '100%'}} placeholder="Number of Items" name="noi" onChange={this.onChange}>
  <option key="select">Select No. of Items</option>
  <option key="1">1</option>
  <option key="2">2</option>
  <option key="3">3</option>
  </select>
  {this.state.error.noi}   
  </div>

  <TextField className="col-12 mb-2" type="text" name="remarks" onChange={this.onChange} onChange={this.onChange} id="standard-basic" label="Item description and consignment value" />
  {this.state.error.remarks}

  <label style={{float: 'left',fontWeight: '400'}}>*Note - Make sure your parcel is hand-carry.</label>
  </div>
  <div className="row rowflexdisable" style={{paddingLeft:'20px',paddingRight:'30px'}}>
  <button class="button col-12" onClick={()=>this.renderRedirect("submit")}><span>Continue</span></button>
  </div>

  </div>

  </div>
  <div className="col-md-6 col-xs-12" style={{ height: '100vh', width: '100%' }}>
  <Map
  center={{ lat: this.props.fromlat, lng: this.props.fromlng }}
  zoom={8}
  places={places1}
  />
  </div>
  </div>

  </div>
  </div>;

  if(this.state.isSubmit)
    {return <Amountcalc 
      isType='courier'
      fromlat={this.props.fromlat}
      fromlng={this.props.fromlng}
      tolat={this.props.tolat}
      tolng={this.props.tolng}
      isLocal={this.props.isLocal}
      isDomestic={this.props.isDomestic}
      address={this.props.address} 
      address1={this.props.address1}
      courier={this.state.courier}
      dataLocal={this.state.data}
      dataDom={this.state.data}
      />
    }
    else if(this.state.isBack)
    {
      return <Map1
      google={this.props.google}
      center={{lat: this.props.fromlat, lng: this.props.fromlat}}
      height='600px'
      zoom={15}
      isType='courier'
      mapHeader='Courier'
      fromlat={this.props.fromlat}
      fromlng={this.props.fromlng}
      tolat={this.props.tolat}
      tolng={this.props.tolng}
      isLocal={this.props.isLocal}
      isDomestic={this.props.isDomestic}
      address={this.props.address} 
      address1={this.props.address1}
      />
    }    
   else return (res);
 }
}

export default App1;

// background: 'linear-gradient(to right, #30CFD0 0%, #330867 100%)',
//    WebkitBackgroundClip: 'text',
//    WebkitTextFillColor: 'transparent' 